html {
  font-size: 14px; }


a {
  color: #333;
  text-decoration: none;

  &:hover {
    color: #333;
    text-decoration: none; }

  &:focus {
    color: #333;
    border: none;
    text-decoration: none;
    outline: none; } }


ul {
  margin-bottom: 0; }

h1,h2,h3,h4,h5,h6 {
  font-weight: bold; }


h1 {
  @media(max-width: 768px) {
    font-size: 30px; } }

h2 {
  @media(max-width: 769px) {
    font-size: 25px; } }

h3 {
  @media(max-width: 768px) {
    font-size: 20px; } }

.material-icons {
  vertical-align: middle; }

.red-colored {
  color: $app-red; }

.hidden-small {
  @media(min-width: 768px) {
    display: none; } }

.hidden-xsmall {
  @media(max-width: 768px ) {
    display: none; } }


.nav-tabs {
  border: none; }

.nav-tabs > li > a {
  border-radius: 0;
  border: none;

  &:hover {
    border-color: transparent;
    background-color: transparent;
    cursor: pointer; } }

.nav-tabs > li.active > a {
  font-weight: bold;
  border: none;
  cursor: pointer;
  color: $app-black;

  &:hover {
    color: inherit;
    background-color: transparent;
    border: none;
    cursor: pointer; }

  &:focus, &:visited {
    border: none; } }


.subsection-nav {
  width: 700px;
  margin: 0 auto;
  li {
    width: 25%;
    vertical-align: top;

    a {
      display: block;
      width: 150px;
      margin: 0 auto;
      text-align: center;
      padding: 0;

      img {
        width: 70px;
        height: 70px;
        display: block;
        margin: 0 auto 15px auto; }

      p {
        font-size: 1.2rem; } } }

  li.active {
    p {
      font-weight: bold; } }


  &--three {
    li {
      width: 33.3%;

      a {
        width: 190px; } } }

  &--big {
    li {
      a {
        img {
          width: 80px;
          height: 80px; } } } }


  @media(max-width: 768px) {
    width: 100%;

    li {
      width: 100%;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0; }

      a {
        width: 100%;

        img {
          margin-bottom: 0; }
        p {
          font-size: 1.1rem;
          margin-top: 25px; }

        &:hover {
          background-color: $app-dark-gray !important; } } } } }

