.app-service {
  &__img {
    margin-top: 60px;
    width: 100%; }

  &__head, &__pres-text {
    max-width: 500px;
    width: 80%;
    margin: 60px auto 30px auto;

    p {
      margin-top: 15px;
      font-size: 1.2rem;
      line-height: 1.5; }

    button {
      margin-top: 15px; } }


  &__pres-features {
    background-color: $app-red;
    padding: 30px 0;
    &--inner {
      width: 80%;
      margin: 0 auto;
      max-width: 600px;
      padding: 15px;
      background-color: white; } }


  &__feature {
    border-bottom: 1px solid $app-red;

    td {
      padding: 15px;
      font-size: 1.2rem;

      i {
        color: $app-red; } } }

  &__features {
    tr:last-child {
      border-bottom: none; } }

  &__carousel {
    width: 80%;
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
    display: block;

    &-img-container {
      width: 150px;
      height: 200px;
      margin: 0 auto;
      position: relative;

      img {
        position: absolute;
        max-width: 100%;
        max-height: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); } } }


  &__rules, &__faq {
    width: 80%;
    margin: 60px auto;
    max-width: 600px; }

  &__faq {
    li {
      margin: 30px 0; } } }

#related-devices {
  .item {
    padding: 60px 0; }

  .carousel-control {
    background: none;
    opacity: 1;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);

    &.right {
      i {
        border-radius: 50%;
        color: $app-text-gray;
        background-color: white;
        position: absolute;
        top: calc(50% - 20px);
        left: 50%;
        font-size: 40px; } }
    &.left {
      i {
        color: $app-text-gray;
        border-radius: 50%;
        background-color: white;
        position: absolute;
        top: calc(50% - 20px);
        right: 50%;
        font-size: 40px; } } } }
