.app-devices {

  &__filters {
    padding: 10px 0;
    border-top: 2px solid $app-dark-gray;
    border-bottom: 2px solid $app-dark-gray;

    >ul {
      >li {
        margin: 0 15px;

        >div {
          >a,p {
            text-transform: uppercase; } } } }
    p {
      margin: 0; }

    .dropdown-toggle {
      cursor: pointer; } }

  &__device {
    display: block;
    width: 100%;
    max-width: 250px;
    margin: 0 auto;
    padding: 20px;
    background-color: $app-light-gray;
    transition: all 50ms linear;

    &:hover {
      box-shadow: 0px 2px 15px rgba(0,0,0,.2); }

    @media(max-width: 768px) {
      max-width: none;
      margin-bottom: 30px;
      padding: 10px;

      >div:last-child {
        text-align: left; } }


    &-img-container {
      width: 180px;
      height: 240px;
      position: relative;

      @media(max-width: 992px) {
        margin-left: -23px; }

      @media(max-width: 768px) {
        margin: 0 auto;
        width: 150px;
        height: 200px;
        margin-top: 35px; }

      img {
        position: absolute;
        max-width: 100%;
        max-height: 100%;

        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%); } }

    &-features {
      text-align: left;
      padding-left: 10px;

      @media(max-width: 768px) {
        padding-left: 0; } }




    .img-normal {
      display: block;
      width: 180px;
      height: 170px;
      margin: 0 auto; }

    .img-extended {
      display: block;
      width: 100px; }


    h3 {
      color: $app-red; }
    p {
      color: $app-text-gray; } }

  &__device-brand {
    margin-top: 22.5px;
    margin-bottom: 0; }

  &__device-color-label {
    font-weight: bold; }


  &__fiter-status {
    h4 {
      margin: 30px 0 60px 0; } } }
